import axios from 'axios';
import fs from 'fs/promises';
import { join } from 'path';
import 'dotenv/config';

// Use the environment variable for the API_KEY
const API_KEY = process.env.VENDUS_API_KEY;
const URL = "https://www.vendus.pt/ws/v1.1/products";

const DATA_DIR = join(process.cwd(), 'app', 'data');
const SEARCH_RESULTS_FILE = join(DATA_DIR, "search_results.json");

const apiClient = axios.create({
  auth: {
    username: API_KEY,
    password: ''
  },
  timeout: 15000,
  maxRedirects: 0,
  headers: {
    'Accept': 'application/json',
    'User-Agent': 'Node.js API Client',
    'Connection': 'keep-alive'
  }
});

const RETRY_CONFIG = {
  maxRetries: 3,
  baseDelay: 1000,
  maxDelay: 8000,
  backoffFactor: 2
};

const sleep = (ms) => new Promise(resolve => setTimeout(resolve, ms));

async function loadSearchResultsFromFile(filePath) {
  try {
    const data = await fs.readFile(filePath, 'utf8');
    const parsedData = JSON.parse(data);
    if (parsedData.found && !(parsedData.found instanceof Map)) {
      parsedData.found = new Map(Object.entries(parsedData.found));
    } else if (!parsedData.found) {
      parsedData.found = new Map();
    }
    return parsedData;
  } catch (error) {
    if (error.code === 'ENOENT') {
      console.log(`File not found: ${filePath}. Returning empty results.`);
    } else {
      console.error(`Error reading or parsing search results file ${filePath}:`, error);
    }
    return {
      searchDate: new Date().toISOString(),
      totalSearched: 0,
      totalFound: 0,
      found: new Map(),
      notFound: []
    };
  }
}

async function saveSearchResultsToFile(filePath, data) {
  try {
    await fs.mkdir(join(filePath, '..'), { recursive: true });

    const dataToSave = { ...data };
    if (dataToSave.found instanceof Map) {
      dataToSave.found = Object.fromEntries(dataToSave.found);
    }
    await fs.writeFile(filePath, JSON.stringify(dataToSave, null, 2), 'utf8');
    console.log(`Results saved to ${filePath}`);
  } catch (error) {
    console.error(`Error saving search results to file ${filePath}:`, error);
    throw error;
  }
}

async function fetchPageFromVendus(page, attempt = 0) {
  if (!API_KEY) {
    return { error: "Vendus API Key not available. Cannot fetch Vendus data." };
  }
  try {
    console.log(`Fetching Vendus products: Page ${page}, Attempt ${attempt + 1}`);
    const response = await apiClient.get(URL, {
      params: {
        page: page,
      },
    });
    return { data: response.data.data };
  } catch (error) {
    if (attempt < RETRY_CONFIG.maxRetries) {
      const delay = Math.min(RETRY_CONFIG.baseDelay * Math.pow(RETRY_CONFIG.backoffFactor, attempt), RETRY_CONFIG.maxDelay);
      console.warn(`Failed to fetch page ${page}. Retrying in ${delay}ms... (Attempt ${attempt + 1}/${RETRY_CONFIG.maxRetries})`);
      await sleep(delay);
      return fetchPageFromVendus(page, attempt + 1);
    }
    console.error(`Error fetching Vendus products page ${page} after ${RETRY_CONFIG.maxRetries} attempts:`, error.message);
    if (error.response) {
      console.error('Response data:', error.response.data);
      console.error('Response status:', error.response.status);
      console.error('Response headers:', error.response.headers);
    } else if (error.request) {
      console.error('No response received:', error.request);
    }
    return { error: error.message };
  }
}

async function updateProductsFromKnownPages(productsToUpdate, maxPages) {
  const foundProducts = new Map();
  const notFoundProducts = new Set(productsToUpdate.keys());
  let totalSearched = 0;

  for (let page = 1; page <= maxPages; page++) {
    console.log(`Processing Vendus page ${page} for product updates...`);
    const { data, error } = await fetchPageFromVendus(page);

    if (error) {
      console.error(`Skipping page ${page} due to error: ${error}`);
      continue;
    }

    if (!data || data.length === 0) {
      console.log(`No more products found on page ${page}. Ending search.`);
      break;
    }

    totalSearched += data.length;

    data.forEach(vendusProduct => {
      const reference = vendusProduct.reference;
      if (productsToUpdate.has(reference)) {
        foundProducts.set(reference, vendusProduct);
        notFoundProducts.delete(reference);
        console.log(`‚úÖ Found and updated product: ${reference}`);
      }
    });

    if (notFoundProducts.size === 0) {
      console.log("All specified products found. Ending search early.");
      break;
    }

    await sleep(200);
  }

  return { found: foundProducts, notFound: notFoundProducts, totalSearched };
}

async function executeProductUpdate() {
  console.log("Starting the product update process in page_reader_products.js...");
  if (!API_KEY) {
    console.error("‚ùå Vendus API Key is not set. Please ensure VENDUS_API_KEY environment variable is configured.");
    process.exit(1);
  }

  try {
    await fs.mkdir(DATA_DIR, { recursive: true });

    const existingResults = await loadSearchResultsFromFile(SEARCH_RESULTS_FILE);
    const productsToUpdate = existingResults.found || new Map();

    if (productsToUpdate.size === 0) {
      console.log("‚ÑπÔ∏è No product references found in 'search_results.json' to update from Vendus. No Vendus API search will be performed.");
      return;
    }

    const { found, notFound, totalSearched } = await updateProductsFromKnownPages(productsToUpdate, 10);

    const updatedFound = {};
    found.forEach((data, ref) => {
      updatedFound[ref] = data;
    });

    const updatedResults = {
      searchDate: new Date().toISOString(),
      totalSearched: totalSearched,
      totalFound: found.size,
      found: updatedFound,
      notFound: Array.from(notFound)
    };

    await saveSearchResultsToFile(SEARCH_RESULTS_FILE, updatedResults);

    console.log('\nüéâ Vendus product data update completed and results saved to search_results.json!');

  } catch (error) {
    console.error('‚ùå Vendus update process failed:', error.message);
    process.exit(1);
  }
}

executeProductUpdate();
